.social-block__link:focus {
  color: #a67ee5;
  background-color: #ffffff;
  border-color: #a67ee5;
  outline: none;
}
.social-block__link:active, .social-block__link:active:focus {
  color: #a67ee5;
  background-color: #ffffff;
  border-color: #ffffff;
}
.social-block__link svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.social-block__link--fb svg {
  width: 0.8rem;
  height: 1.7rem;
}
@media (min-width: 2000px) {
  .social-block__link--fb svg {
    width: 8px;
    height: 17px;
  }
}
.social-block__link--insta svg {
  width: 1.5rem;
  height: 1.5rem;
}
@media (min-width: 2000px) {
  .social-block__link--insta svg {
    width: 15px;
    height: 15px;
  }
}
.social-block__link--vk svg {
  width: 1.7rem;
  height: 1rem;
}
@media (min-width: 2000px) {
  .social-block__link--vk svg {
    width: 10px;
    height: 10px;
  }
}

.screen-bg {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  transform-origin: bottom;
  height: 0;
  background-color: #5f458c;
  z-index: 2;
  transition: height 0 ease;
}
.screen--prizes.active ~ .screen-bg {
  height: 100vh;
}

.accent-text__word {
  display: inline-block;
  margin: 0 5px;
  overflow: hidden;
}

.accent-text__word > span {
  transform: translate3d(0, 1em, 0);
  display: inline-block;
  vertical-align: sub;
}

.screen.active .accent-text.active .accent-text__word > span {
  transform: translate3d(0, 0, 0);
}